/* forms */

input, textarea {
  &::-webkit-input-placeholder {
    color: darken(#9c9c9c,10%);
  }
  &::-moz-placeholder {
    color: darken(#9c9c9c,10%);
  }
  &:-ms-input-placeholder {
    color: darken(#9c9c9c,10%);
  }
  &:-moz-placeholder {
    color: darken(#9c9c9c,10%);
  }
}

button, input, optgroup, select, textarea {
  margin: 0;
  font: inherit;
  color: inherit;
  outline: none;
  &:hover,   &:focus, &:active {
    outline: none;
  }
}

button, input[type=button], input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
  outline: none;
}

label {
  display: inline-block;
  max-width: 100%;
  margin-bottom: 5px;
  font-weight: 700;
}

.form {
  &-group, &__group {
    margin-bottom: 14px;
    &--mb21 {
      margin-bottom: 21px;
    }
  }
  &-control, &__control {
    display: block;
    width: 100%;
    height: 46px;
    padding: 6px 25px;
    font-size: rem(18px);
    line-height: 1.42857143;
    color: $primary-color;
    background-color: $color-white;
    background-image: none;
    background-position: 97% 50% !important;    
    background-repeat: no-repeat;
    background-size: 26px 26px !important;
    border: 1px solid $color-white;
    border-radius: 35px;
    box-shadow: none;
    transition: all .35s;
    font-family: $font-montserrat;
    font-weight: 500;
    &.error {
      border-color: red;
    }
    &+.error {
      position: absolute;
      font-size: 12px;
      color: red;
      font-weight: 500;
      padding-left: 20px;
      margin: 0;
      line-height: 1.2;
    }
  }
  &-label, &__label {
    display: block;
  }
  &-info {
    font-size: rem(10px);
    font-weight: 500;
    text-align: right;
  }
}

textarea {
  &.form-control, &.form__control {
    height: 150px;
    resize: none;
  }
}

.label-checkbox {
  position: relative;
  padding-left: 32px;
  min-height: 19px;
  font-size: rem(16px);
  cursor: pointer;
  margin: 0;
  display: inline-block;
  vertical-align: middle;
  input {
    position: absolute;
    display: none;
    &:checked {
      &~[class*="__input"] {
        background-image: url('data:image/png;base64,');
      }
    }
  }
  &__input {
    position: absolute;
    width: 18px;
    height: 18px;
    left: 0;
    top: 0;
    background-color: $color-white;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 12px 12px;
    border: 1px solid $color-white;
    transition: all 0.35s;
  }
  &__label {
    line-height: 1.2;
    display: block;
  }
}

.has-error {
  .form {
    &-control, &__control {
      color: #dc3545;
      background-image: url("data:image/svg+xml;charset=utf8,%3C!-- Generated by IcoMoon.io --%3E%3Csvg version='1.1' xmlns='http://www.w3.org/2000/svg' width='512' height='512' viewBox='0 0 512 512'%3E%3Ctitle%3E%3C/title%3E%3Cg id='icomoon-ignore'%3E%3C/g%3E%3Cpath fill='%23dc3545' d='M288 64v256h-64v-256h64zM224 448h64v-64h-64v64z'%3E%3C/path%3E%3C/svg%3E") !important;
      padding-right: 40px;
      
    }
  }
}
.has-success {
  .form {
    &-control, &__control {
      color: #28a745;
      background-image: url("data:image/svg+xml;charset=utf8,%3C!-- Generated by IcoMoon.io --%3E%3Csvg version='1.1' xmlns='http://www.w3.org/2000/svg' width='1024' height='1024' viewBox='0 0 1024 1024'%3E%3Ctitle%3E%3C/title%3E%3Cg id='icomoon-ignore'%3E%3C/g%3E%3Cpath fill='%2328a745' d='M927.936 272.992l-68.288-68.288c-12.608-12.576-32.96-12.576-45.536 0l-409.44 409.44-194.752-196.16c-12.576-12.576-32.928-12.576-45.536 0l-68.288 68.288c-12.576 12.608-12.576 32.96 0 45.536l285.568 287.488c12.576 12.576 32.96 12.576 45.536 0l500.736-500.768c12.576-12.544 12.576-32.96 0-45.536z'%3E%3C/path%3E%3C/svg%3E");
      background-position: 98% 50% !important;
      padding-right: 40px;
    }
  }
}